@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
html,
body {
  margin: 0px;
}

body {
  font-family: "Open Sans", sans-serif;
  color: black;
  background-color: #f1f1f1;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

.row::after {
  content: "";
  clear: both;
  display: table;
}

[class*="col-"] {
  float: left;
  padding: 15px;
}

/* For mobile phones: */
[class*="col-"] {
  width: 100%;
}

@media only screen and (min-width: 600px) {
  /* For tablets: */
  .col-s-1 {
    width: 8.33%;
  }
  .col-s-2 {
    width: 16.66%;
  }
  .col-s-3 {
    width: 25%;
  }

  .col-s-4 {
    width: 33.33%;
  }
  .col-s-5 {
    width: 41.66%;
  }
  .col-s-6 {
    width: 50%;
  }
  .col-s-7 {
    width: 58.33%;
  }
  .col-s-8 {
    width: 66.66%;
  }
  .col-s-9 {
    width: 75%;
  }
  .col-s-10 {
    width: 83.33%;
  }
  .col-s-11 {
    width: 91.66%;
  }
  .col-s-12 {
    width: 100%;
  }
}

@media only screen and (min-width: 768px) {
  /* For desktop: */
  .col-1 {
    width: 8.33%;
  }
  .col-2 {
    width: 16.66%;
  }
  .col-3 {
    width: 25%;
  }
  .col-4 {
    width: 33.33%;
  }
  .col-5 {
    width: 41.66%;
  }
  .col-6 {
    width: 50%;
  }
  .col-7 {
    width: 58.33%;
  }
  .col-8 {
    width: 66.66%;
  }
  .col-9 {
    width: 75%;
  }
  .col-10 {
    width: 83.33%;
  }
  .col-11 {
    width: 91.66%;
  }
  .col-12 {
    width: 100%;
  }
  .col-ran {
    width: 37.5%;
  }
}

.clear-float {
  background-color: #e9e9e9;
  padding: 5px 10px;
  border-radius: 5px;
  margin-top: 10px;
}

.text-right {
  float: right !important;
}
.text-left {
  float: left !important;
}

.clear-float:after {
  content: " ";
  visibility: hidden;
  display: block;
  height: 0;
  clear: both;
}

h1,
h2 {
  margin-bottom: 0;
}

.h1 {
  font-size: 14px;
  color: black;
}

.h12 {
  font-size: 13px;
  color: black;
}

.p {
  font-size: 13px;
  color: #a9a9a9;
}

.p2 {
  font-size: 14px;
  color: #a9a9a9;
}

/* ==========================================================================
   02. LOGIN STYLES
   ========================================================================== */

.main-content {
  height: 100vh;
}

.column {
  float: left;
  padding: 10px;
  height: 100vh;
}

.left {
  width: 35%;
  background-color: #fff6d3;
  border-bottom: 8px solid black;
}

.right {
  width: 65%;
  background-color: white;
}

.rowed:after {
  content: "";
  display: table;
  clear: both;
}

@media screen and (max-width: 600px) {
  .column {
    width: 100%;
  }
}

.parent {
  width: 100%;
  height: 100%;
  display: table;
  position: relative;
}

.child {
  padding: 150px 0 0px 50px;
}

@media screen and (max-width: 600px) {
  .child {
    padding: 100px 0 0px 20px;
  }
}

.child h1 {
  max-width: 300px;
  font-family: "Poppins";
  font-size: 25px;
  font-weight: 700;
  line-height: 1.5;
  letter-spacing: 0px;
  color: #001a03;
  opacity: 1;
}

.launcher {
  width: 350px;
}

.flag {
  width: 150px;
  position: absolute;
  right: 5px;
  top: 5px;
}

.secondchild {
  padding: 150px 0 0 130px;
}

@media screen and (max-width: 600px) {
  .secondchild {
    padding: 150px 0 0px 20px;
  }
}

.secondchild h1 {
  max-width: 200px;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.5;
  letter-spacing: 0px;
  color: #001a03;
  opacity: 1;
  margin-bottom: 30px;
}

.secondchild form {
  width: 40%;
}

@media screen and (max-width: 600px) {
  .secondchild form {
    width: 80%;
  }
}

.secondchild label {
  font-size: 10px;
  font-weight: 600;
  line-height: 2.2;
  letter-spacing: 0px;
  color: #2b2d42;
  opacity: 1;
}

.secondchild form input {
  box-sizing: border-box;
  height: 40px;
  width: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  padding: 10px 15px;
  background: #fafafa;
  color: #8d99ae;
  border: 1px solid #8d99ae;
  border-radius: 4px;
  opacity: 1;
  margin-bottom: 5px;
}

.secondchild form input:focus {
  outline: none;
}

.secondchild form button {
  height: 40px;
  width: 100%;
  border-radius: 4px;
  background: black;
  color: white;
  font-size: 10px;
  box-sizing: border-box;
  border: none;
  cursor: pointer;
  text-align: center;
  margin-top: 15px;
}

.error {
  color: red;
  font-size: 10px;
}

.secondchild form button:disabled {
  opacity: 0.2;
}

.header {
  z-index: 32;
  padding: 10px 10px;
  font-size: 14px;
  line-height: 1.5;
  color: #a9a9a9;
  background-color: white;
}

.home-logo {
  width: 40px;
  vertical-align: middle;
}

.Navbar {
  display: flex;
}

.Navbar__Link {
  padding-right: 15px;
  white-space: nowrap;
}

.Navbar__Link span {
  font-size: 14px;
  font-weight: 600;
}

.Navbar__Items {
  display: flex;
  align-items: center;
}

.Navbar__Items--right {
  margin-left: auto;
}

.Navbar__Link-toggle {
  display: none;
}

.Navbar__Link-toggle i {
  font-size: 25px;
}

.circular {
  border-radius: 50px;
  width: 30px;
  height: 30px;
  background-color: #7b66fe;
  padding: 5px 9px;
}

.circular i {
  color: white;
}

.circular .item {
  color: white;
}

.black-bg {
  background-color: black !important;
  padding: 5px 10px !important;
}

@media only screen and (max-width: 768px) {
  .Navbar__Link-brand {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .Navbar__Items,
  .Navbar {
    flex-direction: column;
  }
  .Navbar__Items {
    display: none;
    align-items: baseline;
  }
  .Navbar__Items--right {
    margin-left: 0;
  }

  .Navbar__Link {
    padding-top: 10px;
  }
  .Navbar__ToggleShow {
    display: flex;
  }
  .Navbar__Link-toggle {
    align-self: flex-start;
    display: inline;
    display: initial;
    position: absolute;
    cursor: pointer;
  }
}

.nav-search {
  outline: none;
}

.nav-search {
  -webkit-appearance: textfield;
  box-sizing: content-box;
  -webkit-box-sizing: content-box;
  font-family: inherit;
  font-size: 100%;
}

.nav-search::-webkit-search-decoration,
.nav-search::-webkit-search-cancel-button {
  display: none;
}

.nav-search {
  background: #f1f1f1 url(/static/media/search.1de8f4f9.svg) no-repeat 465px center;
  font-size: inherit;
  font-weight: 400;
  color: black;
  border: 1px solid #f1f1f1;
  box-shadow: none;
  padding: 9px 10px 9px 10px;
  width: 470px;
  height: 13px;
  border-radius: 4px;
  transition: all 0.5s;
}

.nav-search:focus {
  background-color: #f1f1f1;
  border-color: #f1f1f1;
}

.nav-search:-moz-placeholder {
  color: black;
  font-weight: 600;
}
.nav-search::-webkit-input-placeholder {
  color: black;
  font-weight: 600;
}

.title-top-description {
  display: flex;
  align-items: center;
  line-height: 3rem;
}

.logo {
  width: 20px;
}
.media-content {
  width: 100%;
  height: 200px;
  object-fit: cover;
  margin: 5px 0px;
}

.logo-title {
  margin: 0px 10px;
  color: #6e6e6e6e;
}

.card-main {
  padding: 1rem;
  background: #fff;
  border-radius: 7px;
  padding: 0.5rem;
  margin-bottom: 10px;
}

.title-label {
  font-weight: 600;
  font-size: 16px;
  margin-top: 5px;
}

.tablet {
  font-size: 14px;
  color: #7b66fe;
}

.subway-item::after {
  content: "";
  display: inline-block;
  width: 6px;
  height: 6px;
  margin: 2px 15px;
  border-radius: 7.5px;
  background-color: #7b66fe;
}

.margin-tab {
  margin-left: 5px;
}

.abbrev {
  background: black;
  color: #fff;
  font-weight: 400;
  padding: 5px 10px;
  border-radius: 50px;
  width: 28px;
  height: 28px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 5px;
}

.input-flex-main {
  display: flex;
  width: 100%;
}
.search {
  width: inherit;
  border: none;
  background: #e6e6e6;
  color: #a9a9a9 !important;
  border-radius: 30px;
  padding: 8px;
  outline: none;
}

.search-bg {
  background-color: white;
  border: 2px solid #7b66fe;
  border-radius: 8px;
  padding: 15px 8px;
  margin-bottom: 10px;
}

@media only screen and (max-width: 1024px) {
  .search-bg {
    padding: 5px;
  }
}

.search-bg .h1 {
  margin-top: -5px;
  font-size: 16px;
}

.search-bg .p2 {
  color: #a2a2a2;
}

.btn-class {
  background-color: #7b66fe;
  color: white;
  border-radius: 29px;
  padding: 5px 20px;
  margin-right: 20px;
}

.btn-class-two {
  color: #7b66fe;
}

.product-shot {
  width:200px;
}

.padded {
  padding: 0 10px;
}

.padded.up {
  margin-top: 80px;
}

@media only screen and (max-width: 1024px) {
  .padded.up {
    margin-top: 20px;
  }
}

.padded.up p i {
  margin-right: 20px;
}

.padded.up p img {
  margin-right: 20px;
  width: 13px;
  vertical-align: middle;
}

